/*Navigation Bar Styling*/

    .nav-wrapper{
        width: 100%;
        padding-top: 10px;
        padding-left: 10px;
        padding-right: 10px;
        position: fixed;
        /* border-bottom-color: white; */
        border-bottom-width: 1px;
        border-bottom-style: solid;
        z-index: 9000;
        background-color: rgba(0,0,0,0);
    }

    .nav-shadow{
        width: 100%;
        height: 98px;
        position: fixed;
        z-index: -1;
    }

    .nav-wrapper .row{
        margin-bottom: 15px;
    }

    .nav-main-menu{
        text-align: left;
    }

    .nav-first-letter{
        font-size: 24pt;
    }

    .nav-header-text{
        font-size: 20pt;
        font-weight: 500;
        color: white;
        padding: 4px 10px 5px 10px;
        margin-right: 20px;
        text-decoration: none;
        letter-spacing: 1px;
        word-spacing: 5px;
        font-family: 'Montserrat', sans-serif;
        position: relative;
    }

    .nav-header-text:before{
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        bottom: 0;
        left: 0;
        background-color: #FFF;
        visibility: hidden;
        transform: scaleX(0);
        transition: all 0.3s ease-in-out;
    }

    .nav-header-text:hover:before{
        visibility: visible;
        transform: scaleX(.9);
    }

    .nav-header-link{
        text-decoration:none;
        letter-spacing: 1px;
    }

    .nav-home-button{
        border-radius: 15px;
        padding-bottom: 5px;
        height: 100%;
        text-transform: none;
        display: inline-block;
        vertical-align: top;
        border-color: darkslategray;
        background-color: teal;
        border-style: solid;
        border-width: 2px;
    }

    .nav-logo{
        display:inline-block;
        padding-right: 5%;
    }

    .nav-item{
        vertical-align: top;
        text-align: center;
        font-weight: bold;
        font-size: 14pt;
        padding-right: 5%;
        padding-top:12px;
        display: inline-block;
        transition: padding-right .2s;
        &:hover{
            padding-right: 3%;
            transition: padding-right .2s;
        }
    }

    .nav-item-text{
        color:white;
        font-weight: 300;
        position: relative;
        margin-left: 15px;
        margin-right: 0px;
        top:-10px;
        display: inline-block;
        transition: all .2s;
        opacity: 0;
        max-width: 0px;
        white-space: nowrap;
    }

    .nav-item-link{
        cursor: pointer;
        color:white;
        font-weight: 100;
        position: relative;
        display: inline-block;
        &:hover{
            .nav-item-text{
                opacity: 1;
                transition: all .8s;
                max-width: 200px;
            }
        }
    }

    .nav-item-link:before{
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        bottom: 0;
        left: 0;
        background-color: #FFF;
        visibility: hidden;
        transform: scaleX(0);
        transform-origin: left;
        transition: all 0.3s ease-in-out;
    }

    .nav-item-link:hover:before{
        visibility: visible;
        transform: scaleX(1);
    }

    #nav-image{
        height: 35px;
        display: inline-block;
    }

    /* Navigation Bar Styling (Account Side) */

        .nav-main-menu-accounts{
