    .skill-level {
        font-size: .8em;
        z-index: 1;
        line-height: 1;
        width: 80%;
        transition: all 1s;
        height: 60px;
        margin-left: 130px;
        padding-top:8px;
        padding-bottom:8px;
        background-image: repeating-linear-gradient(
        to right,
        #aaa,
        #aaa 1px,
        transparent 1px,
        transparent 9.98%
        );
        
        &:after {
            content: "";
            display: block;
            background-image: linear-gradient(to right,rgb(114, 159, 184) ,rgb(169, 226, 255));
            width: 50px;
            margin-bottom: 10px;
            height: 90%;
            position: relative;
            transition: all 1s;
            top: 50%;
            transform: translateY(-50%);
            transition: background-color .3s ease;
            cursor: pointer;
        }
        }
        
        @for $i from 1 through 100 {
        .percentage-#{$i} {
            &:after {
            $value: ($i * 1%);
            transition: all 1s;
            width: $value;
            }
        }

        .skill-level-holder{
            transition: all 1s;
            &:after {width: 1px !important;}
        }
    }

