// Projects Outlet Styling

    .projects-outlet{
        /* background-color: rgb(87, 99, 110); */
        background-color: rgba(45, 60, 77, 0.75);
        min-height: 1400px;
        width: 100vw;
        max-width: 100vw;
        margin-top:150px;
        padding-bottom: 100px;
        z-index: 9;
        text-align: center;
        // transition: all 1s ease-out;
        // transform: translateZ(0);
        padding-top: 100px;
        color: rgb(230, 230, 230);
        // color: rgb(162, 224, 255);
        // color: rgb(136, 195, 224);
        font-size: 14pt;
        font-weight: 300;
        font-family: 'Montserrat', sans-serif;
        box-sizing: border-box;
    }

    .projects-outlet-container{
        max-width: 2200px;
        width: 100%;
        margin:auto;
    }

    .projects-all-container{
        display: flex;
        justify-content: space-evenly;
        flex-wrap: wrap;
        margin-left: 5%;
        margin-right: 5%;
    }

    .project-ind-container{
        flex: 1 0 33%;
        justify-content: space-around;
        max-width: 500px;
        display: inline-block;
        text-align: center;
        padding: 20px;
        opacity: 1;
        transition: opacity .5s ease;
    }

    .project{
        width: 400px;
        display: inline-block;
        text-align: center;
        padding: 20px;
        opacity: 1;
        transition: opacity .5s ease;
    }

    .project-holder{
        opacity: 0 !important;
        transition: opacity .5s ease;
    }

    .project-image{
        height: 400px;
        width: 400px;
        border-style: solid;
        border-width: 3px;
        border-color: black;
        margin: auto;
        margin-top: 10px;
        margin-bottom: 40px;
    }

    .project-title{
        font-size: 24pt;
        font-weight: 500;
        margin: 20px 0px;
    }

    .project-tools{
        margin: 10px 0px;
        font-size: 16pt;
        font-weight: 500;
    }

    .project-tools-highlight{
        font-weight: 600;
    }

    .project-description{
        margin: 20px 0px;
    }

    .project-links{
        display: flex;
        width: 300px;
        margin: auto;
        margin-top: 30px;
        margin-bottom: 10px;
        justify-content: space-around;
    }